.#{$prefix}-list {
   list-style: none;
   margin: 0;
   padding: 0;

   &--horizontal {
      .#{$prefix}-list__item {
         display: inline;
      }

      &.#{$prefix}-list-dividers {
         &--left > .#{$prefix}-list__item:before {
            @include dot-divider();
         }
         &--left > .#{$prefix}-list__item:first-child:before {
            content: none;
         }
         &--right > .#{$prefix}-list__item:after {
            @include dot-divider();
         }
         &--right > .#{$prefix}-list__item:last-child:after {
            content: none;
         }
      }
   }

   &.#{$prefix}-list-dividers {
      &--top > .#{$prefix}-list__item {
         @include border-divider(border-top);
      }
      &--bottom > .#{$prefix}-list__item {
         @include border-divider(border-bottom);
      }
      &--around > .#{$prefix}-list__item {
         @include border-divider(border);
         + .#{$prefix}-list__item {
            margin-top: $space;
         }
      }
   }

   .#{$prefix}-list-item-divider {
      &--top {
         @include border-divider(border-top);
      }
      &--bottom {
         @include border-divider(border-bottom);
      }
      &--around {
         @include border-divider(border);
      }
   }

   &.#{$prefix}-list-horizontal-fixed {
      display: flex;
      flex-wrap: wrap;

      @for $i from 1 through 12 {
         &--#{$i} .#{$prefix}-list__item {
            @include basis-value($i);
         }
      }
   }
}
