@font-face {
   font-family: 'sitevision-icons';
   src:  url('fonts/sitevision-icons.eot?pyuqss');
   src:  url('fonts/sitevision-icons.eot?pyuqss#iefix') format('embedded-opentype'),
      url('fonts/sitevision-icons.woff2?pyuqss') format('woff2'),
      url('fonts/sitevision-icons.ttf?pyuqss') format('truetype'),
      url('fonts/sitevision-icons.woff?pyuqss') format('woff'),
      url('fonts/sitevision-icons.svg?pyuqss#sitevision-icons') format('svg');
   font-weight: normal;
   font-style: normal;
}

[class^="sv-icon--"], [class*=" sv-icon--"] {
   /* use !important to prevent issues with browser extensions that change fonts */
   font-family: 'sitevision-icons' !important;
   speak: none;
   font-style: normal;
   font-weight: normal;
   font-variant: normal;
   text-transform: none;
   line-height: 1;

   /* Better Font Rendering =========== */
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}


